html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-padding-top: 0rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fixed-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: #666bff;
}
@media (min-width: 768px) {
  .header.fixed-bar {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.1rem 0.2rem 0.3rem #333;
  }
}
.header h1 {
  color: #ffffff;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header .header-container {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (min-width: 768px) {
  .main-nav {
    display: flex;
    gap: 2rem;
  }
}
.main-nav a {
  display: block;
  text-align: center;
  color: #ffffff;
  font-size: 2.4rem;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .main-nav a {
    margin: 0;
  }
}
.main-nav a:hover {
  /* & remove white space */
  color: #ffff4d;
}

.video {
  position: relative;
  max-height: 70rem;
  min-height: 50rem;
  overflow: hidden;
  /* OVERLAY EFFECT: 
      Overlay means to cover the surface of something with a coating. 
      In other words, it is used to set one thing on the top of another.
      More info: https://www.w3schools.com/howto/howto_css_overlay.asp 
  */
}
.video .overlay {
  position: absolute;
  background: #7e02c3;
  background: linear-gradient(270deg, rgba(126, 2, 195, 0.5) 0%, rgba(255, 0, 110, 0.3) 100%);
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  width: auto;
  display: block;
}
@media (min-width: 992px) {
  .video video {
    width: 100%;
  }
}

.contain-video {
  z-index: 10;
}
.contain-video h2 {
  color: #ffffff;
  font-size: 6rem;
  margin: 0;
}
.contain-video p {
  background-color: #ff66b3;
  display: inline-block;
  padding: 1rem 2rem;
  color: #ffffff;
  font-weight: 700;
  font-size: 2.4rem;
  margin: 0;
}

.about-festival {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .about-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.container-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .container-festival {
    margin-top: 0;
  }
}
.container-festival h2 {
  margin-bottom: 2rem;
}
.container-festival .date {
  color: #ff9999;
  font-weight: 700;
}

.lineup {
  background-color: #ff9999;
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #ffffff;
}
.lineup .day {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .container-stage {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.container-stage p {
  color: #000000;
}

.stage {
  padding-top: 2rem;
}
.stage .name-stage:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
}

.edm .name-stage:after {
  background-image: url(../img/icon_edm.png);
}

.rock .name-stage:after {
  background-image: url(../img/icon_rock.png);
}

.calendar {
  margin: 0;
  padding: 0;
  list-style: none;
}
.calendar li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendar li span {
  font-weight: 400;
}

.bg-yellow {
  background-color: #ffff4d;
}
.bg-yellow li:nth-child(odd) {
  background-color: #ffff01;
}

.bg-green {
  background-color: #51e03e;
}
.bg-green li:nth-child(odd) {
  background-color: #34ca21;
}

.gallery {
  padding: 5rem 0;
}
.gallery h3 {
  text-align: center;
}

.gallery-images {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.btn-close {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: #ffffff;
  color: #000000;
  display: grid;
  place-items: center;
  font-weight: 700;
}
.btn-close:hover {
  cursor: pointer;
}

.tickets {
  padding: 5rem 0;
  background-color: #666bff;
}
.tickets h3 {
  text-align: center;
  color: #ffffff;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .tickets .container-tickets {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.pass {
  color: black;
}
.pass .name-pass {
  text-align: center;
  font-size: 2.8rem;
  color: #ffffff;
}
.pass-header, .pass-footer {
  padding: 0.5rem;
}
.pass-header p, .pass-footer p {
  text-align: center;
  color: #ffffff;
  margin: 0;
}
.pass-header {
  background-color: #ff66b3;
}
.pass-header p {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.pass-body {
  background-color: #ffffff;
}
.pass-body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.pass-body ul li,
.pass-body ul :nth-child(odd) {
  background-color: #ffffff;
  padding: 1rem;
  border-bottom: 1px solid #e1e1e1;
}
.pass-footer {
  background-color: #ff66b3;
}
.pass-footer p {
  font-size: 2.4rem;
  font-weight: 700;
}

.site-footer {
  background-color: #000000;
  padding: 1rem;
}
.site-footer p {
  margin: 0;
  text-align: center;
  color: #ffffff;
}
.site-footer p span {
  font-weight: bold;
}